import { Component } from 'react';
import PropTypes from 'prop-types';
import { POST_KIND, genId } from '../shared';

export default class Outline extends Component {

    static propTypes = {
        chunks: PropTypes.array,
        outlineDepth: PropTypes.number
    };

    parseHeader(chunk) {
      if (!chunk || chunk.children) {
        return -1;
      }
      switch(chunk.kind) {
        case POST_KIND.HEADING_1:
        return 1;

        case POST_KIND.HEADING_2:
        return 2;

        case POST_KIND.HEADING_3:
        return 3;

        case POST_KIND.HEADING_4:
        return 4;

        case POST_KIND.HEADING_5:
        return 5;

        case POST_KIND.HEADING_6:
        return 6;

      default:
        return -1;
      }
    }

    parseOutline() {
      const stack = [];
      const outline = [];
      for (let i = 0; i < this.props.chunks.length; i += 1) {
        const chunk = this.props.chunks[i];
        const headerKind = this.parseHeader(chunk);
        if (headerKind >= 1) {
          const newBlock = {
            content: chunk.content,
            kind: headerKind,
            children: []
          };

          // pop the stack
          const popped = stack.pop();
          if (!popped) {
            // this a top level in our strcture
            outline.push(newBlock);
            stack.push(newBlock);
          } else {
            if (popped.kind < headerKind) {
              // this is a child of the popped
              popped.children.push(newBlock);
              stack.push(popped);
              stack.push(newBlock);
            } else {
              // this needs to go a level up
              let parent;
              do {
                parent = stack.pop();
              } while (parent && parent.kind >= headerKind);

              if (parent) {
                // found a parent to attach this to
                parent.children.push(newBlock);
                stack.push(parent);
                stack.push(newBlock);
              } else {
                // no parent so new top-level
                outline.push(newBlock);
                stack.push(newBlock);
              }
            }
          }
        }
      }
      return outline;
    }

    renderBlocks(blocks) {
      if (blocks.length === 0) return <></>
      return (
        <ul>
          {
            blocks
              .filter(block => block.kind <= this.props.outlineDepth)
              .map(block => {
              const id = genId(block.content);
              return (
                <li key={id}>
                  <a href={`#${id}`}>{block.content}</a>
                  {this.renderBlocks(block.children)}
                </li>
              );
            })
          }
        </ul>
      );
    }

    render() {
      const outline = this.parseOutline();
      return this.renderBlocks(outline);
    }
}